@import "sass/index";

$cardWidth: 240px;

.minifiedWinner {
  display: flex;
  position: relative;
  flex-direction: column;

  align-items: center;

  background: use(--info-dark);
  border-radius: 30px;
  overflow: hidden;
  width: $cardWidth;
  height: 210px;

  @media (max-width: $mobileScreen) {
    width: 100%;
  }
}
.logo {
  display: flex;
  background: use(--secondary-main);
  margin-top: 52px;

  width: 48px;
  height: 48px;
  border-radius: 50%;
}

.userName,
.profit {
  font-family: "Gilroy";
  letter-spacing: 0;
  text-align: center;
  line-height: 16px;
}

.userName {
  margin-top: spacing(1.5);

  font-weight: bold;
  font-size: 14px;
  color: use(--template-modal-title-color);
}
.profit {
  margin-top: spacing(2.5);

  font-weight: 600;
  font-size: 24px;
  color: use(--subsidiary-green);
}
.badge {
  display: flex;
  flex-direction: column;

  justify-content: center;
  align-items: center;

  position: absolute;
  top: 0;
  left: 0;

  height: 83px;
  width: 88px;
  border-radius: 0 0 30px 0;
  background: rgba(#0b0218, 0.45);
}
